<template>
  <div style="display: flex; flex-direction: column; align-items: center">
    <h1>修改产品信息</h1>
    <div style="width: 500px">
      <el-form ref="form">
        <el-form-item label="名字">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="EAN">
          <el-input v-model="form.ean"></el-input>
        </el-form-item>
        <el-form-item label="MODLEID">
          <el-input v-model="form.model_id"></el-input>
        </el-form-item>
        <el-form-item label="ASIN">
          <el-input v-model="form.asin"></el-input>
        </el-form-item>
        <el-form-item label="采购价(RMB)">
          <el-input v-model="form.rmb_price"></el-input>
        </el-form-item>
        <el-form-item label="运输价(USD)">
          <el-input v-model="form.ship_fee"></el-input>
        </el-form-item>
        <el-form-item label="商品数量(个)">
          <el-input v-model="form.item_amount"></el-input>
        </el-form-item>
        <el-form-item label="内盒尺寸(cm)长">
          <el-input v-model="form.inner_spec.length"></el-input>
        </el-form-item>
        <el-form-item label="内盒尺寸(cm)宽">
          <el-input v-model="form.inner_spec.width"></el-input>
        </el-form-item>
        <el-form-item label="内盒尺寸(cm)高">
          <el-input v-model="form.inner_spec.height"></el-input>
        </el-form-item>
        <el-form-item label="内箱商品重量(克)">
          <el-input v-model="form.inner_spec.weight"></el-input>
        </el-form-item>
        <el-form-item label="外盒尺寸(cm)长">
          <el-input v-model="form.out_spec.length"></el-input>
        </el-form-item>
        <el-form-item label="外盒尺寸(cm)宽">
          <el-input v-model="form.out_spec.width"></el-input>
        </el-form-item>
        <el-form-item label="外盒尺寸(cm)宽">
          <el-input v-model="tsp"></el-input>
        </el-form-item>
        <el-form-item label="外盒尺寸(cm)宽">
          <el-input v-model="form.out_spec.width"></el-input>
        </el-form-item>
        <el-form-item label="外盒尺寸(cm)宽">
          <el-input v-model="form.out_spec.width"></el-input>
        </el-form-item>
        <el-form-item label="外盒尺寸(cm)高">
          <el-input v-model="form.out_spec.height"></el-input>
        </el-form-item>
        <el-form-item label="外箱商品重量(千克)">
          <el-input v-model="form.out_spec.weight"></el-input>
        </el-form-item>
        <el-form-item label="每箱商品数">
          <el-input v-model="form.unit_per_box"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <el-button @click="sendData" type="primary">提交</el-button>
  </div>
</template>
<script>
export default {
  name: "app",
  components: {},
  computed: {
		tsp: {
			get() {
				return this.form.out_spec.length * 6;
			},
			set(val) {
				this.form.out_spec.length = val / 6;
			}
		},
    
    },
  created() {
    document.body.scrollTop = document.documentElement.scrollTop = 0;
    this.form.row_num = this.$route.params.id;
    console.log(this.form);
    this.$axios({
      method: "get",
      url: "/product/" + this.form.row_num,
    })
      .then((res) => {
        if (res.data) this.form = res.data;

        // console.log(res)
      })
      .catch(
        (error) => console.log(error) //请求失败返回的数据
      );
    // this.form.name = "";
  },
  data() {
    return {
      form_data: {},
      form: {
        row_num: 0,
        ean: 0,
        model_id: "",
        asin: "",
        name: "",
        rmb_price: 0,
        ship_fee: 0,
        item_amount: 0,
        inner_spec: {
          length: 0,
          width: 0,
          height: 0,
          weight: 0,
        },
        out_spec: {
          length: 0,
          width: 0,
          height: 0,
          weight: 0,
        },
        unit_per_box: 0,
      },
      baseurl: "http://1.117.92.68:1099/",
    };
  },
  methods: {
    sendData() {
      console.log(this.form);
      this.form_data.row_num = this.$route.params.id;
      this.form_data.ean = this.form.ean;
      this.form_data.model_id = this.form.model_id;
      this.form_data.asin = this.form.asin;
      this.form_data.name = this.form.name;
      this.form_data.rmb_price = this.form.rmb_price;
      this.form_data.ship_fee = this.form.ship_fee;
      this.form_data.item_amount = this.form.item_amount;
      this.form_data.unit_per_box = this.form.unit_per_box;
      this.form_data.inner_length = this.form.inner_spec.length;
      this.form_data.inner_width = this.form.inner_spec.width;
      this.form_data.inner_height = this.form.inner_spec.height;
      this.form_data.inner_weight = this.form.inner_spec.weight;
      this.form_data.out_length = this.form.out_spec.length;
      this.form_data.out_width = this.form.out_spec.width;
      this.form_data.out_height = this.form.out_spec.height;
      this.form_data.out_weight = this.form.out_spec.weight;

      // this.form.
      this.$message({
        message: "恭喜你，这是一条成功消息",
        type: "success",
      });
      this.$axios({
        method: "post",
        url: "/product",
        data: this.form_data,
      })
        .then((res) => {
          // console.log(res)
          this.tableData = res.data.products;
        })
        .catch(
          (error) => console.log(error) //请求失败返回的数据
        );
    },
  },
};
</script>